body {
    box-sizing: border-box;
    background-image: url(/assets/negative-space-black-forest-dawn.jpg);
    background-repeat: no-repeat, repeat;   
    color: white;
    // background-repeat: no-repeat;
    height: 250vh;
    margin: 0;
    padding: 0;
}

nav {
    display: flex;
    align-items: center;
    justify-content: end;
}

nav ul li {
    list-style-type: none;
    display: inline-block;
    padding: 10px 20px;
}

nav ul li a {
    color: white;
    text-decoration: none;
}

p {
    display: flex;
    color: white;
    font-size: 20px;
    align-items: center;
    justify-content: center;
    min-height: 130px;   
}

svg {
    position: relative;
    border-radius: 50%;
    color: white;
    justify-content: center;
    align-items: center;
    text-align: center;
    top: 80px;
    left: 550px;
    margin: auto;
}

.text {
    background-color: gray;
    color: black;
    position: absolute;
    top: 320px;
    box-shadow: 0 10px 30px -10px rgb(0, 0, 0);
}



.card--light {
    position: absolute;
    background: #eee;
    border-radius: 50%;
    width: 20%;
}

