a {
    position: relative;
    text-decoration: none;
    font-family: 'poppins', sans-serif;
    color: #a0a0a0;
    font-size: 15px;
    letter-spacing: 0.5px;
    padding: 0 10px;
}

a:after {
    content: "";
    position: absolute;
    background-color: white;
    height: 3px;
    width: 0;
    left: 0;
    bottom: -10px;
}

a:hover:after {
    width: 100%;
}

html {
    scroll-behavior: smooth;
